"use client"

import { useState, useEffect } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { BarChart } from "@/components/ui/bar-chart"
import Link from "next/link"

interface DashboardData {
  stats: {
    totalCourses: number;
    activeStudents: number;
    avgCompletionRate: number;
  };
  recentCourses: Array<{
    id: number;
    title: string;
    instructor_name: string;
    enrolled_students: number;
    status: string;
    created_at: string;
  }>;
  recentStudents: Array<{
    id: number;
    full_name: string;
    enrolled_courses: number;
    latest_enrollment: string;
  }>;
  categories: Array<{
    id: number;
    name: string;
    course_count: number;
  }>;
}

export default function DashboardPage() {
  const [data, setData] = useState<DashboardData | null>(null)
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const fetchDashboardData = async () => {
      try {
        const response = await fetch('/api/dashboard/stats')
        if (!response.ok) {
          throw new Error('Failed to fetch dashboard data')
        }
        const data = await response.json()
        setData(data)
      } catch (error) {
        console.error('Error fetching dashboard data:', error)
      } finally {
        setLoading(false)
      }
    }

    fetchDashboardData()
  }, [])

  if (loading) {
    return (
      <div className="p-8">
        <div className="animate-pulse">
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
            {[1, 2, 3].map((i) => (
              <Card key={i} className="p-6">
                <div className="h-4 bg-gray-200 rounded w-1/3 mb-2"></div>
                <div className="h-8 bg-gray-200 rounded w-1/2"></div>
              </Card>
            ))}
          </div>
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
            {[1, 2].map((i) => (
              <Card key={i} className="p-6">
                <div className="h-6 bg-gray-200 rounded w-1/4 mb-4"></div>
                <div className="space-y-3">
                  {[1, 2, 3].map((j) => (
                    <div key={j} className="h-4 bg-gray-200 rounded w-full"></div>
                  ))}
                </div>
              </Card>
            ))}
          </div>
        </div>
      </div>
    )
  }

  if (!data) {
    return (
      <div className="p-8">
        <Card className="p-12 text-center">
          <p className="text-lg text-gray-500">Failed to load dashboard data</p>
        </Card>
      </div>
    )
  }

  const chartData = {
    labels: data.categories.map(cat => cat.name),
    datasets: [
      {
        label: 'Number of Courses',
        data: data.categories.map(cat => cat.course_count),
        backgroundColor: 'rgba(59, 130, 246, 0.5)',
        yAxisID: 'courses'
      }
    ]
  }

  return (
    <div className="p-8">
      <div className="mb-8">
        <h1 className="text-2xl font-bold mb-2">Dashboard</h1>
        <p className="text-gray-600">Overview of your learning management system</p>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
        <Card className="p-6">
          <h3 className="text-sm font-medium text-gray-500 mb-1">Total Courses</h3>
          <p className="text-3xl font-bold">{data.stats.totalCourses}</p>
        </Card>

        <Card className="p-6">
          <h3 className="text-sm font-medium text-gray-500 mb-1">Active Students</h3>
          <p className="text-3xl font-bold">{data.stats.activeStudents}</p>
        </Card>

        <Card className="p-6">
          <h3 className="text-sm font-medium text-gray-500 mb-1">Avg. Completion Rate</h3>
          <p className="text-3xl font-bold">{Math.round(data.stats.avgCompletionRate)}%</p>
        </Card>
      </div>

      <Card className="p-6 mb-8">
        <h2 className="text-lg font-semibold mb-4">Course Distribution</h2>
        <div className="h-[400px]">
          <BarChart data={chartData} />
        </div>
      </Card>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
        <Card className="p-6">
          <div className="flex justify-between items-center mb-6">
            <h2 className="text-lg font-semibold">Recent Courses</h2>
            <Link href="/courses">
              <Button variant="ghost" size="sm">View all</Button>
            </Link>
          </div>
          <div className="space-y-4">
            {data.recentCourses.map((course) => (
              <div key={course.id} className="flex items-center justify-between py-2">
                <div>
                  <p className="font-medium">{course.title}</p>
                  <p className="text-sm text-gray-500">
                    {course.instructor_name} â€¢ {course.enrolled_students} students
                  </p>
                </div>
                <span className={`px-2 py-1 text-xs font-medium rounded-full ${
                  course.status === "published" 
                    ? "bg-green-100 text-green-800"
                    : "bg-gray-100 text-gray-800"
                }`}>
                  {course.status}
                </span>
              </div>
            ))}
          </div>
        </Card>

        <Card className="p-6">
          <div className="flex justify-between items-center mb-6">
            <h2 className="text-lg font-semibold">Recent Students</h2>
            <Link href="/students">
              <Button variant="ghost" size="sm">View all</Button>
            </Link>
          </div>
          <div className="space-y-4">
            {data.recentStudents.map((student) => (
              <div key={student.id} className="flex items-center gap-4 py-2">
                <div className="w-8 h-8 rounded-full bg-gray-200" />
                <div>
                  <p className="font-medium">{student.full_name}</p>
                  <p className="text-sm text-gray-500">
                    Enrolled in {student.enrolled_courses} courses
                  </p>
                </div>
              </div>
            ))}
          </div>
        </Card>
      </div>
    </div>
  )
}
